import React, { useContext } from 'react';
import HmiContext from '../../context/HmiContext';
import ExpansionPanelV2 from '../ExpansionPanelV2/ExpansionPanelV2';

const HmiPurpose: React.FC = () => {
  const hmiContext = useContext(HmiContext);
  const { parentApp } = hmiContext.hmi;

  if (!parentApp) {
    return null;
  }

  if (!parentApp.info.dataUsePurpose) {
    return null;
  }

  return (
    <>
      <ExpansionPanelV2 titleId={'hmi.auth.permissions.purposeWhy'}>
        <div>{parentApp.info.dataUsePurpose}</div>
      </ExpansionPanelV2>
      <hr />
    </>
  );
};

export default HmiPurpose;
