import React, { useContext } from 'react';
import HmiContext from '../../context/HmiContext';
import ExpansionPanelV2 from '../ExpansionPanelV2/ExpansionPanelV2';
import FormatMessage from '../../messages/FormatMessage';

const HmiDuration: React.FC = () => {
  const hmiContext = useContext(HmiContext);
  const { parentApp } = hmiContext.hmi;

  if (!parentApp) {
    return null;
  }

  return (
    <>
      <ExpansionPanelV2 titleId={'hmi.auth.permissions.durationWhen'}>
        <div>
          <FormatMessage
            id={'hmi.auth.permissions.durationTillDisabled'}
            values={{ name: parentApp.info.name }}
          />
        </div>
      </ExpansionPanelV2>
      <hr />
    </>
  );
};

export default HmiDuration;
