import React, { useEffect, useRef } from 'react';
import './ModalV2.scss';
// @ts-ignore
import closeIcon from '../../assets/icons/button_close.svg';
import FormatMessage from '../../messages/FormatMessage';

type Props = {
  open: boolean;
  onClose: () => void;
  titleMessageId: string;
  children: React.ReactNode;
};

export const ModalV2: React.FC<Props> = ({
  open,
  onClose,
  titleMessageId,
  children,
}) => {
  const content = useRef<HTMLObjectElement>(null);

  useEffect(() => {
    const handleClick = (e: MouseEvent) => {
      if (content.current === e.target) {
        onClose();
      }
    };

    document.addEventListener('mousedown', handleClick, false);

    return () => {
      document.removeEventListener('mousedown', handleClick, false);
    };
  }, [onClose]);

  return (
    <div
      id="DsModal"
      ref={content}
      style={open ? { display: 'block' } : {}}
      className="ds-modal"
    >
      <div className="ds-modal-content">
        <div className="ds-modal-header">
          <div className={'ds-modal-text-title'}>
            <FormatMessage id={titleMessageId} />
          </div>
          <button onClick={onClose} className={'ds-modal-close-button'}>
            <img src={closeIcon} alt={'Close modal'} />
          </button>
        </div>
        <div className="ds-modal-body ds-modal-text-content">{children}</div>
        <div className="ds-modal-footer">
          <button onClick={onClose} className={'ds-modal-btn-accent'}>
            <span>
              <FormatMessage id={'hmi.auth.modal.backBtn'} />
            </span>
          </button>
        </div>
      </div>
    </div>
  );
};
